import React, { useEffect, useState } from 'react';
import { IntlProvider } from 'react-intl';
import Russian from './lang/ru.json';
import { Routes, Route } from 'react-router';
import { Home } from './pages/Home';
import { RootState } from './store';
import { useSelector, useDispatch } from 'react-redux';
import {Integrations} from "./pages/Integrations";
import {Docs} from "./pages/Docs";
import {Intro} from "./pages/Docs/pages/Intro";
import {UlsToken} from "./pages/Docs/pages/UlsToken";
import {Staking} from "./pages/Docs/pages/Staking";
import {Roadmap} from "./pages/Docs/pages/Roadmap";
import {Tokenomics} from "./pages/Docs/pages/Tokenomics";
import {Links} from "./pages/Docs/pages/Links";
import {Faq} from "./pages/Faq/Faq";

function App() {
    const locale = useSelector((state: RootState) => state.UIStore.locale);
    const dispatch = useDispatch();
    const [i18nStrings, setI18nStrings] = useState<{}>(Russian);

    useEffect(() => {
        const path =
            window.location.pathname.length > 2 && (window.location.pathname.charAt(3) === '/' || window.location.pathname.charAt(3) === '')
                ? window.location.pathname.slice(1, 3)
                : 'en';

        dispatch({ type: 'changeLocale', value: path });

        const appHeight = () => {
            const doc = document.documentElement;
            doc.style.setProperty('--vh', `${window.innerHeight}px`);
        };

        window.addEventListener('resize', appHeight);

        appHeight();

        return () => {
            window.removeEventListener('resize', appHeight);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        let isMounted = true;

        if (isMounted) {
            setI18nStrings(Russian);
            document.documentElement.setAttribute('lang', 'ru');
        }
        return () => {
            isMounted = false;
        };
    }, [locale]);

    return (
        <IntlProvider messages={i18nStrings} locale={`${locale}`} defaultLocale="ru">
            <Routes>
                <Route path="/">
                    <Route index element={<Home />} />
                </Route>
                <Route path="/:locale">
                    <Route index element={<Home />} />
                </Route>
                <Route path="*" element={<Home />} />
                <Route path="/integrations" element={<Integrations />} />
                <Route path="/docs/" element={<Intro />} />
                <Route path="/docs/intro" element={<Intro/>}/>
                <Route path="/docs/tokenusl" element={<UlsToken/>}/>
                <Route path="/docs/stacking" element={<Staking/>}/>
                <Route path="/docs/roadmap" element={<Roadmap/>}/>
                <Route path="/docs/tokenomics" element={<Tokenomics/>}/>
                <Route path="/docs/links" element={<Links/>}/>
                <Route path="/faq" element={<Faq/>}/>
            </Routes>
        </IntlProvider>
    );
}

export default App;
