import React, { useState, useEffect, useRef } from "react";


import image1 from "../../assets/images/cards/image-1.jpg";
import image2 from "../../assets/images/cards/image-2.jpg";
import image3 from "../../assets/images/cards/image-3.jpg";
import image4 from "../../assets/images/cards/image-4.jpg";
import image5 from "../../assets/images/cards/image-5.jpg";
import image6 from "../../assets/images/cards/image-6.jpg";
const buttons = [{
    name: "All",
    value: "all"
},
    {
        name: "Exchanges",
        value: "exchanges"
    },
    {
        name: "Crosschain Bridges",
        value: "crosschain"
    },
    {
        name: "Wallets",
        value: "wallets"
    }]
const dataCards = [
    {
        category: 'exchanges',
        image: image1,
        title: 'Clever',
        text: 'Simplifies & automates the process of earning bribes and rewards for $UNIT',
        link: '#',
    }, {
        category: 'crosschain',
        image: image2,
        title: 'Convergence',
        text: 'Governance black hole',
        link: '#',
    }, {
        category: 'wallets',
        image: image3,
        title: 'Convex',
        text: 'Boosted UNIT staking. Maximize your yields',
        link: '#',
    }, {
        category: 'exchanges',
        image: image4,
        title: 'Liama Airforce',
        text: 'uCRV Union Bribes',
        link: '#',
    }, {
        category: 'crosschain',
        image: image5,
        title: 'lobby Dao',
        text: 'Designed to improve the voting market of vlCVX and veCRV',
        link: '#',
    }, {
        category: 'wallets',
        image: image6,
        title: 'Paladin',
        text: 'Markets for influence and voting rights across Defi',
        link: '#',
    },
]

export function getinfo() {
    const infoList = dataCards;
    return infoList;
}

export function filterinfo(pokeType) {
    let filtredinfo = getinfo().filter(type => type.category === pokeType);
    return filtredinfo;
}

export default function Cards() {
    const [filtredinfo, setFiltredinfo] = useState(null);
    useEffect(() => {
        setFiltredinfo(getinfo());
    }, []);

    function handleinfo(e) {
        let typeinfo = e.target.value;

        typeinfo !== "all"
            ? setFiltredinfo(filterinfo(typeinfo))
            : setFiltredinfo(getinfo());

        const currTarget = e.currentTarget;

        document.querySelectorAll('.filter-item').forEach( btn =>
            btn.classList.remove('is-active'))
        currTarget.classList.add('is-active');
    }


    return (
        <>
            <div className="section section-xs filter">
                <div className="row gx-3">
                    {buttons &&
                        buttons.map((type, index) => (
                            <>
                                <div className="col-md-3">
                                    <button key={index} value={type.value} onClick={handleinfo} className="filter-item font-minecraft">
                                        {type.name}
                                    </button>
                                </div>
                            </>
                        ))}
                </div>
            </div>

            <div className="cards">
                <div className="row g-3">
                    {filtredinfo &&
                        filtredinfo.map(type => (
                            <div className="col-md-4 " key={type.id}>
                                <div className="cards-item">
                                    <div className="cards-item-content">
                                        <div className="cards-item-title">
                                            {type.title}
                                        </div>

                                        <div className="cards-item-text">
                                            {type.text}
                                        </div>
                                        <a href={type.link} className="cards-item-link font-minecraft">
                                            Link
                                        </a>
                                    </div>

                                    <div className="cards-item-image ">
                                        <img src={type.image} alt="image"/>
                                    </div>
                                </div>
                            </div>
                        ))}


                </div>
            </div>



        </>
    );
}
